import _ from "lodash";
import React from "react";
import axios from "axios";
import styled from "styled-components";
import Head from "../components/Head";
import Rank from "../components/Rank";
import RankSunburst from "../components/RankSunburst";

const Topbar = styled.div`
  height: 20px;
`;

const Title = styled.div`
  font-size: 1.5em;
  padding: 3px 10px 3px 10px;
  background: #ffffff;
  display: inline-block;
  border-radius: 3px;
`;

const Document = styled.div`
  background-image: url("./static/background.jpg");
  background-size: cover;
  background-position: center center;
  height: 100vh;
`;

const Mask = styled.div`
  width: 100%;
  height: 100%;
  background-color: rgba(10, 10, 10, 0.6);
  display: flex;
  flex-direction: column;
`;

const Container = styled.div``;

const Sunburst = styled.div``;

const RankPanel = styled(Rank)`
  margin-right: 0;
`;

const Footer = styled.div`
  text-align: center;
`;

export default class Home extends React.PureComponent {
  state = {
    data: null,
  };

  componentDidMount() {
    this.loadData();
  }

  async loadData() {
    const res = await axios.get("./static/data.json");
    const raw = await res.data;
    const fields = _.pullAt(raw, [0])[0];
    const data = _.map(raw, item => _.mapKeys(item, (__, key) => fields[key]));
    this.setState({ data });
  }

  renderSunburst() {
    const { data } = this.state;
    if (!data) return null;

    return <RankSunburst data={data} />;
  }

  renderRank() {
    const { data } = this.state;
    if (!data) return null;

    return <RankPanel data={data} />;
  }

  render() {
    return (
      <Document>
        <Head title="資料申請小幫手" />
        <Mask>
          <Container className="container">
            <Topbar />
            <Title>資料申請小幫手</Title>
            {this.renderRank()}
          </Container>
        </Mask>
      </Document>
    );
  }
}
